export const convertDate = (dateString) => {
  if (!dateString) return "";

  const date = new Date(dateString);

  const options = {
    weekday: "long", // Th trong tun
    year: "numeric", // Nm
    month: "long", // Thng
    day: "numeric", // Ngy
    hour: "2-digit", // Gi
    minute: "2-digit", // Pht
    second: "2-digit", // Giy
  };

  return date.toLocaleString("vi-VN", options);
};
