import * as yup from "yup";

export const schema = yup.object().shape({
  name: yup.string().required("Vui lng nhp tn sn phm"),
  description: yup.string().required("Vui lng nhp m t sn phm"),
  category: yup.string().required("Vui lng chn danh mc cho sn phm"),
  productImg: yup
    .mixed()
    .test(
      "Vui lng chn hnh nh",
      "Chn t nht 1 hnh nh",
      (files) => files?.length > 0
    ),
  subCategory: yup.array().of(
    yup.object().shape({
      tag: yup.string().required("Vui lng chn kiu dng sn phm"),
      model: yup.array().of(
        yup.object().shape({
          color: yup.string().required("Vui lng chn mu sc sn phm"),
          skus: yup.array().of(
            yup.object().shape({
              sku: yup.string().required("Vui lng nhp SKU cho sn phm"),
              size: yup.string().required("Vui lng chn size cho sn phm"),
              price: yup
                .number()
                .required("Vui lng nhp gi tin")
                .typeError("Vui lng nhp s"),
            })
          ),
        })
      ),
    })
  ),
});
