import * as yup from "yup";

export const registerSchema = yup.object().shape({
  username: yup.string().required("Vui lng nhp tn ng nhp"),
  email: yup.string().email().required("Vui lng nhp Email"),
  password: yup.string().required("Vui lng nhp mt khu"),
  password_confirmation: yup.string().required("Vui lng nhp mt khu"),
});

export const loginSchema = yup.object().shape({
  email: yup.string().email().required("Vui lng nhp Email"),
  password: yup.string().required("Vui lng nhp mt khu"),
});

export const verifyEmailSchema = yup.object().shape({
  email: yup.string().email().required("Vui lng nhp Email"),
  otp: yup.string().required("Vui lng nhp OTP"),
});

export const changePasswordSchema = yup.object().shape({
  password: yup.string().required("Vui lng nhp mt khu"),
  password_confirmation: yup.string().required("Vui lng nhp mt khu"),
});

export const resetPasswordLinkSchema = yup.object().shape({
  email: yup.string().email().required("Vui lng nhp Email"),
});

export const resetPasswordSchema = yup.object().shape({
  password: yup.string().required("Vui lng nhp mt khu"),
  password_confirmation: yup.string().required("Vui lng nhp mt khu"),
});
