import * as yup from "yup";
import { PHONEREG } from "../../../../constants/regex";

export const schema = yup.object().shape({
  name: yup.string().required("Vui lng nhp h tn"),
  phone: yup.string().matches(PHONEREG, "Vui lng nhp s in thoi"),
  province: yup.string().required("Vui lng chn tnh/thnh ph"),
  district: yup.string().required("Vui lng chn qun/huyn"),
  address: yup.string().required("Vui lng ghi a ch c th"),
  paymentMethod: yup.string().required("Vui lng chn phng thc thanh ton"),
  shippingPrice: yup.string().required("Vui lng chn phng thc vn chuyn"),
  note: yup.string(),
  userId: yup.string(),
});
